import {
  IColaboradoresListaView,
  IColaboradoresListaController,
} from "../../contracts/Cadastros/ColaboradoresListaContract";
import axios from "../../plugins/axios";
import { HeaderAction } from "@/models/header";

export class ColaboradoresListaController
  implements IColaboradoresListaController {
  _view: IColaboradoresListaView;

  constructor(view: IColaboradoresListaView) {
    this._view = view;
  }

  importItens(): void {
    this._view.loading(true);

    axios
      .get("Colaboradores/Integracao")
      .then((response: any) => {
        this._view.loading(false);
        if (response.data.sucesso) {
          //this._view.showMsg("Importação completa.")
          this.carregarItens();
        } else {
          this._view.showMsg(response.data.message);
        }
      })
      .catch((e: any) => {
        this._view.loading(false);
        this._view.showMsg(e);
      });
  }

  carregarItens(): void {
    this._view.loading(true);

    axios
      .get("Colaboradores")
      .then((response: any) => {
        this._view.loading(false);
        response.data.forEach((Element: any) => {
          Element.resetPass = [
            new HeaderAction(
              "mdi-backup-restore",
              "primary",
              "Reset Senha",
              "resetPass",
              "200px"
            ),
          ];
        });
        this._view.showItens(response.data);
      })
      .catch((e: any) => {
        this._view.loading(false);
        this._view.showMsg(e);
      });
  }

  resetPass(codigo: string): void {
    this._view.loading(true);
    axios
      .get("Colaboradores/ResetSenha/" + codigo)
      .then((response: any) => {
        this._view.loading(false);
        if (response.data.sucesso) {
          this._view.showMsg("Senha alterada.");
        } else {
          this._view.showMsg(response.data.message);
        }
      })
      .catch((e: any) => {
        this._view.loading(false);
        this._view.loading(false);
        this._view.showMsg(e);
      });
  }
}
