

































import Vue from "vue";
import KLoading from "../../../components/KLoading.vue";
import KDialog from "../../../components/KDialog.vue";
import KDataList from "../../../components/KDataList.vue";

import { Display } from "../../../configuration/display";

import { Header, HeaderIcon, HeaderAction } from "../../../models/header";
import { Headers } from "../../../models/headers";

import {
  IColaboradoresListaView,
  IColaboradoresListaController
} from "../../../contracts/Cadastros/ColaboradoresListaContract";

import { ColaboradoresListaController } from "../../../controllers/Cadastros/ColaboradoresListaController";

import Component from "vue-class-component";

@Component({
  components: {
    KLoading,
    KDialog,
    KDataList
  }
})
export default class ColaboradoresLista extends Vue
  implements IColaboradoresListaView {
  _controller?: IColaboradoresListaController;

  dialogLoading = false;
  dialogMessage = false;
  textError = "";

  loading(visible: boolean): void {
    this.dialogLoading = visible;
  }
  showMsg(msg: string): void {
    this.textError = msg;
    this.dialogMessage = true;
  }
  showItens(list: any): void {
    this.itens = list;
  }
  fecharDialogMessage() {
    this.dialogMessage = false;
  }

  resetPass(item: any) {
    this._controller!.resetPass(item.codigo);
  }

  isMobile = false;

  headers = new Headers([
    new Header("Código", "codigo", false, false),
    new Header("Nome", "nome", false, false),
    new Header("Email", "email", false, false),
    new Header("Reset Senha", "resetPass", true, false, "center", false)
  ]);

  itens = [];

  importLista() {
    this._controller!.importItens();
  }

  created() {
    this.isMobile = Display.isMobile();
    this._controller = new ColaboradoresListaController(this);

    this._controller.carregarItens();
  }
}
